<template lang="html">
    <!--  优惠券使用说明 -->
    <div id="direction">
        <header-common :title="viewTitle" :iconType="iconType" :rightParams="rightParams"></header-common>
        <div id="discountWrap" v-html="data"></div>
    </div>

</template>

<script>
import headerCommon from '../../components/header/headerCommon.vue'
import ajax from './../../vueResource.ajax.js'

export default {
    data () {
        return {
            viewTitle: '优惠券使用说明',
            iconType: 'icon-fanhui',
            rightParams: {},
            data: {}
        }
    },
    components: {
        'header-common': headerCommon
    },
    beforeCreate () {
        window.scroll(0,0)
        this.$store.commit('hideFoot')
    },
    created () {
        this.getData()
    },
    methods: {
        getData () {
            ajax.ajax({
                'vue': this,
                'port': 'CouponInstructions',
                'type': 'get',
                'statusOk': function (res, v) {
                    console.log(res.data.data)
                    v.data = res.data.data
                },
                'statusError': function (res, v) {
                }
            })
        }
    }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
#direction
    #discountWrap
        margin-top:50px
        padding-left:0
        /*height:50px*/
        line-height:25px
        color:#999
        overflow:hidden
        p
            text-indent:2rem
</style>



