<template>
    <div class="index-wrap">
        <headerIndex v-on:showSearch="showSearchReal"></headerIndex>
        <div class="searchWrap" v-if="searchShow">
            <span class="iconfont icon-sousuo1"></span>
            <div class="inputWrap">
                <input type="text" name="" v-model="searchModel">
            </div>
            <label @click="searchServe">确定</label>
            <label @click="cancelServe">取消</label>
            <!-- <div class="searchMask"></div> -->
        </div>
        <div class="swiper-container show-img"  ref="showimg">
            <div class="swiper-wrapper">
                <div class="swiper-slide" v-if="HomeAdvers.length > 0" v-for="item in HomeAdvers">
                    <a @click="gotoArticle(item.linkId)"><img :src="item.displayPictureUrl" /></a>
                </div>
                <div class="swiper-slide" v-if="HomeAdvers.length === 0">
                    <router-link to="/index"><img src="../assets/images/default_homeImg.jpg" /></router-link>
                </div>
            </div>
            <div v-if="HomeAdvers.length > 0" class="swiper-pagination"></div>
        </div>
        <div class="carinfo-wrap">
            <div class="curCarInfo" v-if="isLogin && defaultCar && defaultCar.id">
                <div class="CarInfoWrap" @click="$router.push('/user/changeCurCarIndex')">
                    <div class="carLogo">
                        <img :src="defaultCarImg" >
                    </div>
                    <div class="carInfo iconfont icon-chevron-copy-copy-copy-copy-copy-copy">
                        <div class="carID">{{defaultCar.displayLicensePlate}}</div>
                        <div class="carDesc">{{defaultCar.carName}}</div>
                        <div class="changeCarHand">更换</div>
                    </div>
                </div>
            </div>
            <div class="curCarInfo" v-else>
                <div class="addCar" @click="$router.push('/user/addCarInfo')">
                    <span class="iconfont icon-jia"></span>请先添加车辆
                </div>
            </div>
        </div>
        <div class="nav-wrap">
            <router-link to="/serve/index?id=1">保养</router-link>
            <router-link to="/serve/fix?id=2">维修</router-link>
            <router-link to="/serve/hairdressing?id=3">美容</router-link>
            <router-link to="/serve/tire?id=4">轮胎</router-link>
            <router-link to="/serve/sprinkle?id=5">无痕钣喷</router-link>
            <router-link to="/roadHelp">救援</router-link>
            <router-link to="/indexInsurance">车险</router-link>
            <router-link to="/indexInspection">年检</router-link>
            <!-- <a @click="openNJ">年检</a> -->
            <router-link to="/user/balance">充值</router-link>
            <router-link to="/user/issure">常见问题</router-link>
        </div>
        <div class="write-space"></div>
        <div class="plan commonWrap" v-if="isLogin">
            <div class="md-title">
                保养计划表
                <div class="title-right" @click="amendKM">
                    当前公里数:<label>{{defaultCar&&defaultCar.totalKm?defaultCar.totalKm:''}}</label>
                    <span class="title-right-icon"></span>
                </div>
            </div>
            <div class="plan-wrap commonContent">
                <ul>
                    <li v-for="item in BYPlan"  @click="$router.push('/serveIntroduce/' + item.serviceProductId )"><img :src="item.serviceIconPictureUrl" /><span>{{item.serviceProductName}}</span></li>
                </ul>
            </div>
            <div class="plan-btn-wrap">
                <div class="check" @click="$router.push('/plan')">查看保养手册</div>
                <div class="cart-in" @click="addCarts()">加入购物车</div>
            </div>
        </div>
        <div class="write-space"></div>
        <div class="violation commonWrap">
            <div class="md-title">
                违章查询
            </div>
            <div class="commonContent">
                <p>新违章自动提醒  无忧车务从现在开始</p>
                <div class="wz-search">
                    <!-- <input type="text" name="car_num" value="" placeholder="填写车架号后六位" /> -->
                    <button class="wzcx iconfont icon-sousuo"  @click="queryCJH"><label>立即查询</label></button>
                </div>
            </div>
        </div>
        <div class="write-space"></div>
        <div class="recommend commonWrap">
            <div class="md-title">
                推荐
            </div>
            <div class="commonContent">
                <div class="art-list" v-for="article in RecommendArticles" @click="onArticleDetail(article.articleId)">
                    <img :src="article.pictureUrl" />
                    <div class="info">
                        <p class="tt">{{article.title}}</p>
                        <p class="desc">{{article.short}}</p>
                    </div>
                </div>

            </div>
        </div>
        <div class="addImgWrap">
            <div class="swiper-container" ref="addimg">
                <div class="swiper-wrapper">
                    <div class="swiper-slide" v-for="item in AdverSpace1Advers">
                        <!-- <router-link to="item.linkId"><img :src="item.displayPictureUrl" /></router-link> -->
                        <a @click="gotoArticle(item.linkId)"><img :src="item.displayPictureUrl" /></a>
                    </div>
                </div>
            </div>
        </div>
        <div class="write-space"></div>
        <!-- <div class="insurance commonWrap" v-if="isLogin && defaultCar">
            <div class="md-title">
                车险
            </div>
            <div class="commonContent">
                车辆保险到期提醒
                <mt-switch @change="change" v-model="defaultCar.isCarInsuranceNotify"></mt-switch>
            </div>
        </div>
        <div class="write-space"></div>
        <div class="insurance commonWrap" v-if="isLogin && defaultCar">
            <div class="md-title">
                年检
            </div>
            <div class="commonContent">
                年检到期提醒
                <mt-switch @change="change2" v-model="defaultCar.isAnnualInspectionNotify"></mt-switch>
            </div>
        </div> -->
        <div class="menuList">
            <p class="phone iconfont icon-chevron-copy-copy-copy-copy-copy-copy">
                <span class="phoneShow" @click="call(kf_phone)">客服电话 <em class="phoneNum">{{kf_phone}}</em></span>
            </p>
        </div>
        <p class="tip">服务时间：{{kf_time}}</p>
        <div class="write-space"></div>
        <div class="write-space-50"></div>
        <iframe scrolling="no" id="queryCJH" v-if="cjhShow" src="http://www.cheshouye.com/api/weizhang/"></iframe>
        <div id="cjhBack" v-if="cjhShow" class="iconfont icon-fanhui" @click="hiddenCJH"></div>
        <!-- <setInspection v-if="NJToggle" v-on:closeNJ="closeNJRel"></setInspection>
        <setInsurance v-if="BXToggle" v-on:closeBX="closeBXRel"></setInsurance> -->
    </div>
</template>

<script type="text/ecmascript-6">
    import headerIndex from '../components/header/headerIndex.vue'
    import setInspection from './setInspection.vue'
    import setInsurance from './setInsurance.vue'
    import Bscroll from 'better-scroll'
    import 'mint-ui/lib/style.min.css'
    import { Switch, Toast, MessageBox } from 'mint-ui'
    import Swiper from 'swiper'
    import 'swiper/dist/css/swiper.min.css'
    import ports from './../port.js'
    import ajax from './../vueResource.ajax.js'

    export default {
        data () {
            return {
                // 保险配置
                insuranceValue: false,
                // 年检配置
                examineValue: false,
                // 轮播图
                HomeAdvers: [],
                AdverSpace1Advers: [],
                // 推荐文章
                RecommendArticles: [],
                // 保养手册
                BYPlan: [],
                // 第一次监测数据变化
                first1: 0,
                first2: 1,
                // 车架号查询
                cjhShow: false,
                // 搜索
                searchShow: false,
                searchModel: '',
                // 车辆图片
                defaultCarImg: '',
                // 保险开关
                BXToggle: false,
                // 年检开关
                NJToggle: false,
                kf_phone: '',
                // 工作时间
                kf_time: ''
            }
        },
        components: {
            headerIndex,
            'mt-switch': Switch,
            'setInspection':setInspection,
            'setInsurance':setInsurance
        },
        beforeCreate () {
            window.scroll(0, 0)
            this.$store.commit('showFoot')
            // 轮播图
            var that = this
            this.$http.get(ports.port.AppHomeAdvers).then((res) => {
                if (res.data.success === true) {
                    this.HomeAdvers = res.data.data
                    setTimeout(function () {
                        that._initScroll()
                    }, 500)
                } else {
                    // window.$.PageDialog.ok('获取轮播图失败')
                }
            })
            // 广告位
            this.$http.get(ports.port.AppAdverSpace1Advers).then((res) => {
                if (res.data.success === true) {
                    this.AdverSpace1Advers = res.data.data
                    setTimeout(function () {
                        that._addImgScroll()
                    }, 500)
                } else {
                    // Toast('获取广告图失败')
                }
            })
            // 获取推荐文章列表
            var articleParams = '?page=1&' + 'pageSize=100'
            this.$http.get(ports.port.GetRecommendArticles + articleParams).then((res) => {
                if (res.data.success === true) {
                    var dat = res.data.data
                    // for (var i = 0; i < dat.length; i++) {
                    //     dat[i].articleId = '/article/' + dat[i].articleId
                    // }
                    this.RecommendArticles = res.data.data
                } else {
                    // Toast('获取推荐文章失败')
                }
            })
        },
        created () {
            if(window.localStorage.serveType){
                window.localStorage.serveType = null
            }
            if (window.localStorage.userToken) {
                this.getDefaultCarInfo()
                this.getCarList()
            }
            this.getKF_phone()
            var _url = encodeURIComponent('http://smlc.wzyichu.com/index.html')
            ajax.ajax({
                'vue': this,
                'port': 'GetWxPayConfig',
                'type': 'get',
                'params_url': '?url=' + _url,
                'statusOk': function (res, v) {
                    var params = JSON.parse(res.data.data)
                    wx.config({
                        debug: false,
                        appId: params.appId,
                        timestamp: params.timestamp,
                        nonceStr: params.noncestr,
                        signature: params.signature,
                        // jsApiList: ['chooseWXPay']
                        jsApiList: ['onMenuShareTimeline','onMenuShareAppMessage','chooseWXPay']
                    })
                    wx.ready(function(){
                        wx.onMenuShareAppMessage({
                            title: '尚门理车', // 分享标题
                            desc: '尚门理车', // 分享描述
                            link: 'http://m.smliche.com', // 分享链接
                            imgUrl: 'http://m.smliche.com/100.jpg' // 分享图标
                        });
                        wx.onMenuShareTimeline({
                            title: '尚门理车', // 分享标题
                            link: 'http://m.smliche.com', // 分享链接
                            imgUrl: 'http://m.smliche.com/100.jpg' // 分享图标
                        });
                    })
                },
                'statusError': function (res, v) {
                }
            })
        },
        computed: {
            defaultCar () {
                return this.$store.state.user.userDefaultCar
            },
            isLogin () {
                return window.localStorage.userToken
            },
            insuranceValue () {
                if (this.defaultCar) {
                    return this.defaultCar.isCarInsuranceNotify
                }
            },
            examineValue () {
                if (this.defaultCar) {
                    return this.defaultCar.isAnnualInspectionNotify
                }
            }
        },
        watch: {
            defaultCar () {
                this.getBYData()
                this.getDefaultCarImg ()
            }
        },
        methods: {
          // 获取客服电话
           getKF_phone () {
                ajax.ajax({
                   'vue': this,
                   'port': 'GetTelephone',
                   'type': 'get',
                   'statusOk': function (res, v) {
                       v.kf_phone = res.data.data.telePhone
                       v.kf_time = res.data.data.serviceTime
                   },
                   'statusError': function (res, v) {
                   }
               })
           },
            call (num) {
                window.location.href = 'tel:' + num
            },
            // 关闭
            closeBXRel () {
                this.BXToggle = false
            },
            closeNJRel () {
                this.NJToggle = false
            },
            openNJ () {
                this.NJToggle = true
            },
            // 开启搜索
            showSearchReal () {
                this.searchShow = true
            },
            // 搜索服务
            searchServe () {
                this.$router.push('/searchServe?key=' + this.searchModel)
            },
            cancelServe () {
                this.searchShow = false
            },
            // 批量添加购物车
            addCarts () {
                var arr = []
                for (var i = 0; i < this.BYPlan.length; i++) {
                    arr.push(this.BYPlan[i].serviceProductId)
                }

                var params = {
                    customerCarId: this.defaultCar.id,
                    serviceProductIds: arr,
                    countyId: this.$store.state.user.curAreaID,
                    longitude: window.sessionStorage.lng,
                    latitude: window.sessionStorage.lat
                }
                ajax.ajax({
                    'vue': this,
                    'port': 'AddServiceProductsToCart',
                    'type': 'post',
                    'req_params': params,
                    'statusOk': function (res, v) {
                        var tmpdata = res.data.data
                        window.$.PageDialog.ok('已添加可用服务')
                    },
                    'statusError': function (res, v) {
                        window.$.PageDialog.ok(res.data.errorMessage)
                    }
                })
            },
            queryCJH () {
                // this.cjhShow = true
                console.log(window.localStorage.curAreaID)
                if(!window.localStorage.curAreaID){
                    window.$.PageDialog.ok('正在定位，请稍候')
                    return
                }
                //this.$router.push('/queryRegulations')
                ajax.ajax({
                   'vue': this,
                   'port': 'GetOffender',
                   'type': 'get',
                   'params_url': '?countyId=' + window.localStorage.curAreaID,
                   'statusOk': function (res, v) {
                       console.log(res.data.data)
                       if(res.data.data.appUrl === ''){
                           window.$.PageDialog.ok('当前区域未开通违章查询功能')
                           return
                       }
                       // v.ADUrl = res.data.data.appUrl
                      window.location.href = res.data.data.appUrl
                   },
                   'statusError': function (res, v) {
                   }
               })
            },
            hiddenCJH () {
                this.cjhShow = false
            },
            gotoArticle (id) {
                if (parseInt(id) > 0) {
                    this.$router.push('/article/' + id)
                } else {
                    id = encodeURIComponent(id)
                    this.$router.push('/advertisementView?ADUrl=' + id)
                }
            },
            change () {
                if (this.insuranceValue === false) {
                    // this.$router.push('/setInsurance')
                    // 保险开关
                    this.BXToggle = true
                } else {
                    var params = {
                        id: this.$store.state.user.userDefaultCar.id,
                        isCarInsuranceNotify: false
                    }
                    ajax.ajax({
                        'vue': this,
                        'port': 'UpdateCarInsurance',
                        'type': 'post',
                        'req_params': params,
                        'statusOk': function (res, v) {
                            if (res.data.success === true) {
                                window.$.PageDialog.ok('您已关闭车险到期提醒')
                            }
                        },
                        'statusError': function (res, v) {
                        }
                    })
                }
            },
            change2 () {
                if (this.examineValue === false) {
                    // this.$router.push('/setInspection')
                    this.NJToggle = true
                } else {
                    var params = {
                        id: this.$store.state.user.userDefaultCar.id,
                        isAnnualInspectionNotify: false
                    }
                    ajax.ajax({
                        'vue': this,
                        'port': 'UpdateAnnualInspection',
                        'type': 'post',
                        'req_params': params,
                        'statusOk': function (res, v) {
                            if (res.data.success === true) {
                                window.$.PageDialog.ok('您已关闭年检到期提醒')
                            }
                        },
                        'statusError': function (res, v) {
                        }
                    })
                }
            },
            _initScroll () {
                this.viewScroll = new Swiper(this.$refs.showimg, {
                    direction: 'horizontal',
                    pagination: '.swiper-pagination',
                    autoplay: 3000,
                    autoplayDisableOnInteraction:false
                })
            },
            _addImgScroll () {
                this.viewScroll = new Swiper(this.$refs.addimg, {
                    direction: 'horizontal',
                    autoplay: 3000,
                    autoplayDisableOnInteraction:false
                })
            },
            gotoCarlist () {
                this.$router.push('/user/carList')
            },
            amendKM () {
                // 修改里程
                MessageBox.prompt('请输入总里程数', '').then(({ value, action }) => {
                    var params = {
                        id: this.defaultCar.id,
                        totalKm: value
                    }
                    ajax.ajax({
                        'vue': this,
                        'port': 'UpdateTotalKm',
                        'type': 'post',
                        'req_params': params,
                        'statusOk': function (res, v) {
                            v.$store.state.user.userDefaultCar.totalKm = value
                            window.$.PageDialog.ok('修改成功')
                        },
                        'statusError': function (res, v) {
                            window.$.PageDialog.ok('操作失败，请稍后重试')
                        }
                    })
                })
            },
            // 获取默认车辆数据
            getDefaultCarInfo () {
                if (!this.$store.state.user.userDefaultCar.id) {
                    ajax.ajax({
                        'vue': this,
                        'port': 'GetDefaultCustomerCar',
                        'type': 'get',
                        'statusOk': function (res, v) {
                            v.$store.state.user.userDefaultCar = res.data.data
                            v.$store.state.user.curCarNum = v.$store.state.user.userDefaultCar.displayLicensePlate
                            v.getBYData()
                        },
                        'statusError': function (res, v) {
                            if (!res.data.data) {
                                MessageBox.confirm('您需要先添加车辆信息<br>是否现在添加?').then(action => {
                                    v.$router.push('/user/addCarInfo')
                                })
                            }
                        }
                    })
                } else {
                    var listData = this.$store.state.user.userCarList
                    var curCarID = this.$store.state.user.curCarID
                    if (!curCarID) {
                        curCarID =this.$store.state.user.userDefaultCar.id
                    }
                    for (var i = 0; i < listData.length; i++) {
                        if (listData[i].id === curCarID) {
                            this.$store.state.user.userDefaultCar = listData[i]
                            this.getBYData()
                        }
                    }
                }
            },
            getDefaultCarImg () {
                ajax.ajax({
                    'vue': this,
                    'port': 'GetCarPicture',
                    'type': 'get',
                    'params_url': '?carId=' + this.defaultCar.carId,
                    'statusOk': function (res, v) {
                        v.defaultCarImg = res.data.data
                    },
                    'statusError': function (res, v) {
                    }
                })
            },
            getCarList () {
                ajax.ajax({
                    'vue': this,
                    'port': 'GetCustomerCars',
                    'type': 'get',
                    'statusOk': function (res, v) {
                        var tmpdata = res.data.data
                        // 保存车辆列表
                        v.$store.state.user.userCarList = tmpdata
                    },
                    'statusError': function (res, v) {
                    }
                })
            },
            onArticleDetail (id) {
                // 跳转到文章详情
                this.$router.push('/article/' + id)
            },
            // 获取保养数据
            getBYData () {
                if (!this.defaultCar) {
                    return
                }
                this.getDefaultCarImg()
                ajax.ajax({
                    'vue': this,
                    'port': 'GetMaintenanceTips',
                    'type': 'get',
                    'statusOk': function (res, v) {
                        var tmpdata = res.data.data
                        var totalKM = v.defaultCar.totalKm
                        for (var i = 0; i < tmpdata.length; i++) {
                            if (tmpdata[i].startKilometers < totalKM && tmpdata[i].endKilometers >= totalKM) {
                                if (tmpdata[i].serviceProducts) {
                                    v.BYPlan = tmpdata[i].serviceProducts
                                }
                            }
                        }
                    },
                    'statusError': function (res, v) {
                        window.$.PageDialog.ok('保养手册不存在')
                    }
                })
            }
        }
    }
</script>

<style lang="stylus" rel="stylesheet/stylus">
    @import "../assets/css/index.styl"
</style>



