<template lang="html">
    <!--  优惠券 -->
    <div id="discount">
        <header-common :title="viewTitle" :iconType="iconType" :rightParams="rightParams"></header-common>
        <div id="discountWrap">
            <mt-navbar  v-model="selected">
                <mt-tab-item id="1">可用券</mt-tab-item>
                <mt-tab-item id="2">已用/过期</mt-tab-item>
            </mt-navbar>

            <mt-tab-container :swipeable="swipeable"  v-model="selected">
                <mt-tab-container-item id="1">
                    <div class="list">
                        <div class="slideBar" ref="slideBar1">
                            <div class="item" v-for="item in ableData" data-id="item.campaignId">
                                <div class="left">{{item.couponTypeId === 2? '￥':''}}<span>{{item.couponTypeId === 2?item.effectiveAmount:item.effectiveAmount*10}}</span>{{item.couponTypeId === 1? '折':''}}</div>
                                <div class="center"></div>
                                <div class="right">
                                    <h4>{{item.couponTypeName}}</h4>
                                    <p>1、满{{item.conditionAmount }}时可用</p>
                                    <p>2、有效期至{{item.expiresOn.split('T')[0]}}</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </mt-tab-container-item>
                <mt-tab-container-item id="2" class="dated">
                    <div class="list">
                        <div class="slideBar" ref="slideBar2">
                            <div class="item" v-for="item in unAbleData">
                                <div class="left">{{item.couponTypeId === 2? '￥':''}}<span>{{item.couponTypeId === 2?item.effectiveAmount:item.effectiveAmount*10}}</span>{{item.couponTypeId === 1? '折':''}}</div>
                                <div class="center"></div>
                                <div class="right">
                                    <h4>{{item.couponTypeName}}</h4>
                                    <p>1、满{{item.conditionAmount }}时可用</p>
                                    <p>2、有效期至{{item.expiresOn.split('T')[0]}}</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </mt-tab-container-item>
            </mt-tab-container>
        </div>
    </div>

</template>

<script>
import headerCommon from '../../components/header/headerCommon.vue'
// import ports from './../../port.js'
import ajax from './../../vueResource.ajax.js'
import Bscroll from 'better-scroll'
import { Navbar, TabItem, TabContainer, TabContainerItem, Toast, InfiniteScroll } from 'mint-ui'

export default {
    data () {
        return {
            viewTitle: '优惠券',
            iconType: 'icon-fanhui',
            rightParams: {
                path: '/user/direction',
                desc: '使用说明'
            },
            swipeable: false,
            selected: '1',
            // 可用数据
            ableData: [],
            // 不可用数据
            unAbleData: [],
            pageAble: 1,
            pageDisable: 1,
            // 是否在加载
            isLoading: false
        }
    },
    computed: {
    },
    components: {
        'header-common': headerCommon,
        'mt-navbar': Navbar,
        'mt-tab-item': TabItem,
        'mt-tab-container': TabContainer,
        'mt-tab-container-item': TabContainerItem
    },
    beforeCreate () {
        window.scroll(0,0)
        this.$store.commit('hideFoot')
    },
    created () {
        this.getDiscount(true, 1)
        this.loadMore()
    },
    methods: {
        loadMore () {
            var that = this
            // 上一步坐标 本次坐标
            var oldy, cury
            // 滚动条的高度
            window.addEventListener('scroll', function (event) {
                oldy = cury
                var scrollTop = document.body.scrollTop
                cury = scrollTop
                var clientHeight
                if (that.selected === '1') {
                    if (that.$refs.slideBar1) {
                        clientHeight = that.$refs.slideBar1.clientHeight
                    }
                } else {
                    if (that.$refs.slideBar2) {
                        clientHeight = that.$refs.slideBar2.clientHeight
                    }
                }
                // console.log(clientHeight)
                if (!clientHeight) {
                    return
                }
                if (oldy >= cury) {
                    return
                }
                if (scrollTop + window.innerHeight >= clientHeight) {
                    // 触发加载数据
                    // console.log('bottom')
                    if (that.selected === '1') {
                        that.getDiscount(true, that.pageAble)
                    } else {
                        that.getDiscount(false, that.pageDisable)
                    }
                }
            })
        },
        getDiscount (val, page) {
            console.log(val, page)
            if (this.isLoading) {
                return
            }
            this.isLoading = true
            var params = '?dto.isUseable=' + val + '&dto.page=' + page + '&dto.pageSize=10'
            ajax.ajax({
                'vue': this,
                'port': 'GetCurrentCoupons',
                'type': 'get',
                'params_url': params,
                'statusOk': function (res, v) {
                    console.log(res.data)
                    v.isLoading = false
                    if (res.data.data.length <= 0) {
                        // window.$.PageDialog.ok('没有更多')
                        return
                    }
                    // Toast('加载成功')
                    if (val === true) {
                        v.ableData = v.ableData.concat(res.data.data)
                        v.pageAble ++
                    } else {
                        v.unAbleData = v.unAbleData.concat(res.data.data)
                        v.pageDisable ++
                    }
                },
                'statusError': function (res, v) {
                    window.$.PageDialog.ok('获取折扣券失败')
                }
            })
        }
    },
    watch: {
        selected (cur, old) {
            console.log(cur)
            if (cur === '1') {
                this.getDiscount(true, this.pageAble)
            } else {
                this.getDiscount(false, this.pageDisable)
            }
        }
    }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
#discount
    #discountWrap
        margin-top:98px
        .mint-navbar
            position:fixed
            top:45px
            width:100%
            border-bottom:1px solid #ddd
            .mint-tab-item-label
                font-size:16px
                color:#666
        .mint-tab-item.is-selected
            border-color:#c7a770
            .mint-tab-item-label
                color:#c7a770
        .mint-tab-container
            overflow:auto
            position:static
            .list
                position:absolute
                top:97px
                bottom:10px
                z-index:-1
                width:100%
                /*overflow:hidden*/
                .slideBar
                    padding-top:10px
                    .item
                        margin:0 10px 10px 10px
                        /*height:102px*/
                        position:relative
                        padding-left:117px
                        div
                            height:102px
                        .left
                            position:absolute
                            left:0
                            top:0
                            background-color:#fff
                            width:100px
                            text-align:center
                            line-height:100px
                            border:1px solid #ddd
                            border-right:none
                            font-family:"微软雅黑"
                            font-size:13px
                            color:#c7a770
                            span
                                font-size:26px
                        .center
                            position:absolute
                            left:101px
                            top:0
                            background:url("../../assets/images/bg_disc2.png") no-repeat
                            background-size:17px 100px
                            width:17px
                        .right
                            background-color:#fff
                            border:1px solid #ddd
                            border-left:none
                            padding-left:15px
                            h4
                                margin-top:15px
                                margin-bottom:8px
                                font-size:20px
                                color:#666
                            p
                                color:#999
                                font-size:14px
                                line-height:20px
            .dated
                .list
                    .slideBar
                        .item
                            .left
                                color:#ccc
                                span
                                    color:#ccc
                            .right
                                h4
                                    color:#ccc
                                p
                                    color:#ccc


</style>



