            button
                display:inline-block
                width:90px
                height:36px
                line-height:36px
                text-align:center
                background-color:#c7a770
                color:#fff
                float:right
                border:none
                margin:7px 12px 0 0
                font-size:16px
    .cartmask
        position:fixed
        width:100%
        height:100%
        background-color:#f5f5f5
        z-index:100
        top:0
        left:0
</style>



