<template lang="html">
    <div class="agreement">
        <header-common :title="viewTitle" :iconType="iconType" :rightParams="rightParams" :paths="close" v-on:closeXY="closeXYREL"></header-common>
        <div class="messageDesc">
            <div class="desc2" v-html="aboutus">
            </div>
        </div>
    </div>
</template>

<script>
import headerCommon from 'components/header/headerCommon.vue'
import ajax from './../vueResource.ajax.js'
import { Toast } from 'mint-ui'

export default {
    data () {
        return {
            viewTitle: '服务协议',
            iconType: 'icon-fanhui',
            rightParams: {
                path: 'close',
                closeEmit: 'closeXY'
            },
            close: 'close',
            aboutus: ''

        }
    },
    beforeCreate () {
        window.scroll(0,0)
        this.$store.commit('hideFoot')
    },
    computed: {
    },
    created () {
        ajax.ajax({
            'vue': this,
            'port': 'ConditionsOfUse',
            'type': 'get',
            'statusOk': function (res, v) {
                console.log(res.data.data)
                v.aboutus = res.data.data
            },
            'statusError': function (res, v) {
            }
        })
    },
    components: {
        'header-common': headerCommon
    },
    methods: {
        closeXYREL () {
            this.$emit('closeXYInParent')
        }
    }
}
</script>

<style lang="css" lang="stylus" rel="stylesheet/stylus">
.agreement
    position:fixed
    top:0
    left:0
    padding:10px 2%
    background-color:#fff
    height:100%
    width:96%
    overflow-y:auto
    z-index:101
    .tt
        height:40px
        line-height:40px
        font-size:18px
        color:#000
        overflow:hidden
    .tm
        font-size:15px
        color:#999
    .desc2
        margin-top:20px
        padding-left:0
        /*height:50px*/
        line-height:25px
        color:#999
        overflow:hidden
        p
            text-indent:2rem
</style>



